body {
  border: 8px solid #171F3F; }
html {
  font-family: Poppins; }
.header {
  height: 95vh;
  max-width: 55vw;
  margin: 5rem 0 0 5rem; }
.header__pic {
  width: 120px;
  height: 120px;
  border-radius: 50%;
