 }  //   background-color: #CBD2D9
.header__h1 {
  font-weight: 600;
  font-size: 2.3rem;
  line-height: 46px;
  color: #171F3F; }
.header__paragraph {
  font-weight: 300;
  font-size: 1rem;
  line-height: 30px;
  margin-bottom: 4rem;
  color: #52606D; }
.header__anchor {
  color: #0593FB;
  text-decoration: underline; }
.header__email {
  color: #0593FB;
  border-bottom: 2px solid #0593FB;
  text-decoration: none; }


