html {
  line-height: 1.15;
  font-size: 16px;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

body {
  border: 8px solid #171F3F; }

html {
  font-family: Poppins; }

.header {
  height: 95vh;
  max-width: 55vw;
  margin: 5rem 0 0 5rem; }

.header__pic {
  width: 120px;
  height: 120px;
  border-radius: 50%; }

.header__h1 {
  font-weight: 600;
  font-size: 2.3rem;
  line-height: 46px;
  color: #171F3F; }

.header__paragraph {
  font-weight: 300;
  font-size: 1rem;
  line-height: 30px;
  margin-bottom: 4rem;
  color: #52606D; }

.header__anchor {
  color: #0593FB;
  text-decoration: underline; }

.header__email {
  color: #0593FB;
  border-bottom: 2px solid #0593FB;
  text-decoration: none; }

.skills {
  margin: 0 auto;
  height: auto;
  width: 87vw; }

.skills__h2 {
  font-size: 2rem;
  text-align: left; }

.skills__part {
  display: grid;
  grid-template-columns: 40% 60%;
  grid-column-gap: 4rem; }

.skills__container {
  border-top: 2px solid rgba(203, 210, 217, 0.6);
  padding-top: 7rem; }

.skills__list {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-row-gap: 0.5rem;
  list-style: none;
  font-size: 1rem;
  margin-top: 0; }

.skills__list--green {
  list-style-image: url("../assets/img/circleGreen.png"); }

.skills__list--purple {
  list-style-image: url("../assets/img/circlePurple.png"); }

.skills__list--blue {
  list-style-image: url("../assets/img/circleBlue.png"); }

.skills__part--ousted {
  margin: 7rem 0; }

.skills__h3 {
  font-size: 1.7rem;
  color: #171F3F;
  margin-top: -1.5rem; }

.skills__h3--green::before {
  content: "";
  border-top: 3px solid #06C847;
  width: 4rem;
  display: block;
  border-radius: 0.5rem;
  margin-bottom: 1rem; }

.skills__h3--purple::before {
  content: "";
  border-top: 3px solid #653CAD;
  width: 4rem;
  display: block;
  border-radius: 0.5rem;
  margin-bottom: 1rem; }

.skills__h3--blue::before {
  content: "";
  border-top: 3px solid #2251CC;
  width: 4rem;
  display: block;
  border-radius: 0.5rem;
  margin-bottom: 1rem; }

li {
  line-height: 1.5rem; }

.projects {
  background-color: #F5F7FA;
  padding: 8rem 0;
  margin-top: 5rem; }

.projects__wrapper {
  margin: 0 auto;
  height: auto;
  max-width: 87vw; }

.projects__h2 {
  font-size: 2rem;
  text-align: left; }

.projects__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  grid-row-gap: 2rem;
  grid-column-gap: 1rem;
  margin-top: 4rem; }

.project-card {
  width: 35vw;
  height: auto;
  padding: 3rem;
  box-shadow: 0px 0.5rem 1rem rgba(24, 41, 67, 0.09);
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  position: relative; }

.btn-wrapper {
  background-color: #F5F7FA;
  width: 100%;
  height: 25%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem; }

.project-card__btn {
  position: relative;
  color: #0593FB;
  border: none;
  background-color: transparent;
  font-weight: 500; }

.btn-demo::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../assets/img/Demo.png");
  position: absolute;
  left: -24px;
  bottom: 0; }

.btn-github::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../assets/img/Code.png");
  position: absolute;
  left: -24px;
  bottom: 0; }

.list-wrapper {
  background-color: #F5F7FA;
  width: 100%;
  height: 25%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem; }

ul.testul {
  list-style: none;
  display: flex;
  flex-direction: row; }
